import React, { Component, Fragment } from "react";
import Head from "next/head";
import Page from "../layouts/main";
import makePath from "../util/make-path";

class TermsOfUse extends Component {
  constructor(props){
    super(props);
    this.sections = [
      'YOUR ACCEPTANCE OF THIS AGREEMENT',
      'CHANGES TO THIS AGREEMENT',
      'PERMITTED USERS AND ACCESS',
      'MISPRINTS AND ERRORS, PRICES AND PURCHASES',
      'NO ADVICE OR SOLICITATION',
      [
        'DISCLAIMER, LIABILITY EXCLUSION, LIABILITY LIMITATION, RELEASE AND INDEMNITY',
        [
          'DISCLAIMER',
          'LIABILITY EXCLUSION',
          'LIMITATION OF LIABILITY',
          'RELEASE',
          'INDEMNITY'
        ]
      ],
      'LOGIN NAMES AND PASSWORDS',
      'TERMINATION OF THIS AGREEMENT AND THE WEB SITE',
      'INFORMATION SUBMISSIONS',
      'OWNERSHIP AND PERMITTED USE OF THE WEB SITE',
      'PERSONAL INFORMATION PRIVACY',
      'TRADE-MARK INFORMATION',
      'OTHER SITES',
      'LINKING AND FRAMING THE WEB SITE',
      'POSTINGS AND UNSOLICITED SUBMISSIONS',
      'GOVERNING LAW AND DISPUTE RESOLUTION',
      'OTHER MATTERS'
    ]
  }
  render() {
    return (
      <Page id="TermsOfUse" menu={{solidStyle: true}} ref={el => this.Page = el}>
        <div className="ContentContainer">
          <div className="col-12 col-md-10">
            <h2 className="dark padding">Terms of Use</h2>

            <p className="dark mini">Date of Last Update of this agreement: May 22, 2019</p>
            <p className="dark mini">Please read this agreement carefully. It governs your use of the Hospall Private Home Care Inc. (“<b>Hospall</b>”) web site located at <a href="https://www.hospall.com" className="inline">www.hospall.com</a> (the “<b>Web Site</b>”) and the services and goods provided through the Web Site. It exempts Hospall and other persons from liability or limits their liability and contains other important provisions that you should read. Each time you use the Web Site you acknowledge and signify that you have read, understood, and agreed to be bound by this agreement. If you do not agree with each provision of this agreement each time you use the Web Site, you may not use the Web Site.</p>
            <p className="dark mini">The Web Site offers information, tools and resources to manage the care of persons with age-related and physically debilitating needs. Hospall does not provide medical advice through the Web Site or through any services offered on the Web Site. We are not a medical referral service and the Web Site does not provide medical referrals or make recommendations or endorsements for any healthcare providers.</p>
            <p className="dark mini">Each time you use the Web Site, the text of this agreement, as it then reads, will govern your use. Accordingly, when you use the Web Site you should check the date of this agreement and review any changes since the last time you visited. The changes will be clearly shown in the revised document for ease of reference.</p>
            <p className="dark mini padding">This agreement contains the following provisions:</p>

            <h5 className="dark padding">Contents</h5>
            <ol>
              { this.sections.map((section, index) => {
                  const renderLink = (title, prefix) =>
                    <Fragment>
                      { prefix && <span style={{ marginRight: 10 }}>{prefix}</span> }
                      <a className="inline" href={`/terms-of-use#${makePath(title)}`}>{ title }</a>
                    </Fragment>
                  if (typeof section === 'string') {
                    return <li>{renderLink(section)}</li>
                  } else {
                    let result = section[1].map((sec, idx) => <Fragment>{renderLink(sec, `${index+1}.${idx+1}`)}<br/></Fragment> )
                    return (
                      <Fragment>
                        <li>{ renderLink(section[0]) }</li>
                        { result }
                      </Fragment>
                    )
                  }
                })
              }
            </ol>



            <h5 className="dark padding">SECTION 1: PRIVACY COMMITMENT</h5>
            <p className="dark mini">Hospall Private Homecare Inc. (“<b>Hospall</b>”) is committed to ensuring that the information it holds is protected and that confidentiality is maintained. Hospall’s duties include collecting, using, disclosing, retaining, and destroying personal information as it relates to providing health care services and otherwise caring for you.</p>
            <p className="dark mini padding">Hospall’s Privacy Management Program is governed by the <a className="inline" href="https://www.priv.gc.ca/en/privacy-topics/privacy-laws-in-canada/the-personal-information-protection-and-electronic-documents-act-pipeda/" target="_blank">Personal Information Protection and Electronic Documents Act (PIPEDA)</a> and the <a className="inline" href="https://www.ontario.ca/laws/statute/04p03" target="_blank">Personal Health Information Protection Act (PHIPA)</a>. Hospall may also be governed by additional applicable provincial and professional standards.</p> 

            <h5 className="dark padding">SECTION 2: ACCOUNTABILITY</h5>
            <p className="dark mini">While all Hospall employees are accountable for maintaining the privacy of information obtained in the course of their employment, Hospall’s Privacy Officer is responsible for overall compliance. Hospall staff receive careful training on privacy practices and our own Privacy Management Program.</p>
            <p className="dark mini">In order to provide you with good care and service, Hospall’s employees endeavor to ensure that all personal information is accurate, complete, and up to date. We need your assistance in doing this. Please let us know if any information relevant to your health care needs to be changed. However, we will not continue updating your information once you have been discharged.</p>
            <p className="dark mini padding">As required, Hospall will amend this document and its Privacy Management Program when changes to privacy legislation or best practices are known.</p>


            <h5 className="dark padding">SECTION 3: WHAT PERSONAL INFORMATION WE COLLECT AND WHY</h5>
            <p className="dark mini">Hospall staff will collect the personal information required to provide you with the care and service requested and to respond to questions and inquiries you have. Such information may include your name, address, contact information (on yourself and other designated persons), your health information number, and the name of your family physician and any other health care providers. For all services that you request, we may ask you specific questions to help us determine how you manage at home with your current health status, including your ability to be independent in the residence.</p>
            <p className="dark mini">When you request or require home support or personal support services, we will collect information regarding your health and ability to perform activities of daily living. This allows us to assess how best to support you. If you require nursing services, we will complete a more thorough assessment of your health to determine the level of care that you will require.</p>
            <p className="dark mini">If you make inquiries of Hospall online, we may ask for your name, email address and any other information that we feel is reasonably necessary to answer your questions.</p>
            <p className="dark mini padding">If at any time we require more or different information, or your information is required for a purpose other than the care and service discussed with you, unless otherwise permitted or restricted by law, you will be contacted.</p>

            <h5 className="dark padding">SECTION 4: CONSENT</h5>
            <p className="dark mini">We will obtain your consent – either express or implied – before collecting your personal information. Implied consent depends on what is reasonably anticipated in the circumstances. For example, if you ask Hospall to attend at your house, it is implied that we will collect your address for purposes of attending at your house.</p>
            <p className="dark mini">When your case begins, we will request that you give us your express written consent for the services that we will be providing you. This is typically done by way of a Client Consent and can include your permission regarding the release of information that we require. Before you fill out this form, our supervisor or nurse will discuss it with you and, at this point, you can advise us if there are certain people with whom you do not want your information shared with.</p>
            <p className="dark mini">Once your consent is given, it is deemed to be given throughout the entirety of our service, however, you may withdraw your permission or consent in part or in whole, at any time during your care and service. Changing your consent may restrict our ability to provide the requested services. Should this occur, you will be informed about how your consent change will affect our services. In order to change the terms of your consent, or to withdraw, simply contact the Hospall office.</p>
            <p className="dark mini padding">For those individuals who are unable to provide consent, e.g. minors, those who are seriously ill or mentally handicapped persons, consent will be obtained by an authorized authority, e.g. a legal guardian, power of attorney or the executor of your estate.</p>

            <h5 className="dark padding">SECTION 5: HOW WE USE YOUR PERSONAL INFORMATION</h5>
            <p className="dark mini">Your personal information will be used so that we can provide you with the health care services you request and work with your other health care professionals. Using the Client Consent form, our nurse will review the information and record your wishes.</p>
            <p className="dark mini padding">Your information may be reviewed with respect to auditing and quality improvement services, but this may be done without reference to specific people. The purpose of such a review is to monitor our performance and improve our services.</p>

            <h5 className="dark padding">SECTION 6: WHO WE SHARE YOUR PERSONAL INFORMATION WITH</h5>
            <p className="dark mini">When you start service with Hospall, and you sign the Client Consent, we discuss with you who we may share your information with. We will typically share your information with those individuals that we consider the members of your “multidisciplinary care team” or “circle of care”. We may also share your information as otherwise permitted or required by law. Not all employees of Hospall have access to your personal information, even though they are employees of Hospall, and you are a client of Hospall.</p>
            <p className="dark mini">If there are people to whom you do not want us to share your information with, please let us know.</p>
            <p className="dark mini padding">In those rare circumstances where we are required by law to share your information, Hospall will verify the lawful authority requesting the information and only provide what is reasonably necessary for the stated purpose of such request.</p>

            <h5 className="dark padding">SECTION 7: HOW LONG WE KEEP YOUR PERSONAL INFORMATION</h5>
            <p className="dark mini">Hospall will continue to have access to your information even after your services have been discontinued. We retain all discontinued client records for a minimum of 10 years, or longer if required by provincial regulation. We retain children’s records until the child reaches the age of majority plus 10 years. These records whether at our offices or off site, are stored in a manner that are designed to ensure ongoing security. Hospall uses off site storage facilities that cite privacy practices which are in compliance with relevant privacy legislation and only after a Confidentiality Agreement has been signed with the facility. Records are kept of the location of files retained, so that we may retrieve them as required. At the end of the 10-year period, all information that we have, regardless of format, is destroyed in a confidential manner.</p>
            <p className="dark mini padding">In a circumstance where the records are created by Hospall and then retained by other health care organizations, such organizations will maintain the information according to their own retention schedule. Accordingly, access requests will be redirected to the relevant organization in possession of the information.</p>

            <h5 className="dark padding">SECTION 8: SAFEGUARDS</h5>
            <p className="dark mini">Hospall has many measures in place designed to ensure that your information remains confidential, and to comply with requirements under PIPEDA, PHIPA and other applicable laws. Confidentiality applies to all of your information, regardless of whether we store it electronically, in paper format or simply within the knowledge of our staff.</p>
            <p className="dark mini">Staff members complete privacy training at the commencement of their employment and throughout the course of their employment. In addition, they sign a confidentiality agreement and are not permitted to share any information beyond what is required to exercise their duties at Hospall.</p>
            <p className="dark mini">Electronic information is password protected and firewalled. Unauthorized access is restricted through the implementation of safeguards for the storage of information. We also limit access to personal information to those individuals that require it to complete your services. In this regard, we manage our employees and contractors strictly to ensure that information is treated with the utmost care.</p>
            <p className="dark mini">Hospall protects hard copy files by storing them in our office where access is restricted to staff with the necessary authority. Outside of working hours, the files are kept in a locked and secure area.</p>
            <p className="dark mini">In order to protect faxes from going astray, they are sent to pre-programmed numbers and include a confidentiality sheet that outlines what to do if received in error. Similarly, our emails contain a statement informing the receiver what to do if the message is received in error. Both faxes and emails contain the number for Hospall’s Privacy Officer in case errors occur.</p>
            <p className="dark mini padding">Hospall is not responsible for the privacy practices of organizations linked on our website.</p>

            <h5 className="dark padding">SECTION 9: ACCESS TO YOUR PERSONAL INFORMATION</h5>
            <p className="dark mini">Subject to certain limitations, you are able to view your personal information at any time. This can be done by sending a written request to Hospall’s Privacy Officer. You may also request a copy of your file by writing to your local Hospall branch. We invite you to view your records with one of our nurse managers so that they may be available to answer any of your questions.</p>
            <p className="dark mini">In order to maintain our privacy standards, we may need information to identify the person who is making a request for the information. This is only used to make sure that the requesting person is authorized to obtain the information.</p>
            <p className="dark mini padding">When Hospall receives a request for information, we aim to respond to it within 30 days, subject to any limitations that may arise at law. If your access to information is refused for any reason, we will provide you with the reasons for such refusal. A reasonable fee may be charged based on the information that is being requested.</p>

            <h5 className="dark padding">SECTION 10: YOUR RIGHT TO CORRECT YOUR PERSONAL INFORMATION</h5>
            <p className="dark mini padding">You may contact Hospall in writing to request a correction of any errors that may be in your personal information. If the requested correction cannot be made, we will notify you of the reasons for this.</p>

            <h5 className="dark padding">SECTION 11: CONTACT INFORMATION - PRIVACY OFFICE</h5>
            <p className="dark mini"><b>If You Need More Information about Hospall and its Privacy Practices</b></p>
            <p className="dark mini">Anyone can contact Hospall, at the contact information below, with respect to a complaint, question, concern or compliment relating to Hospall’s information handling practices. Any complaints made to Hospall will be investigated thoroughly and action will be taken if the complaint is found to be justified.</p>
            <p className="dark mini">If you have any questions or concerns about your personal information, or if you would like more information about Hospall’s policies and practices for managing and handling personal information, you are invited to contact Hospall’s Privacy Office by email at <a className="inline" href="mailto:michelle@hospall.com">michelle@hospall.com</a>, by calling <a className="inline" href="tel:+1-800-208-8480p101">1-800-208-8480 ex 101</a> or in writing to Hospall Private Homecare Inc., 28 Claudview St, King City, ON L7B 0C6, Attention: Privacy Officer.</p>
            <p className="dark mini">You are also entitled to contact your provincial Privacy Commissioner’s Office if you feel this is necessary. While we would appreciate the opportunity to do what we can to address your concerns, we would also assist you in contacting your Privacy Commissioner’s Office if you believe this is warranted.</p>
            <p className="dark mini padding">Latest Revision Date: May 22, 2019</p>
          </div>
        </div>
      </Page>
    )
  }
}

export default TermsOfUse
